body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #fcf9ed;
}

.App_div {
  background: #ffffff;
  padding: 10px;
  margin: 150px 300px;

  box-shadow: 2px 2px rgb(212, 204, 204), -0.05em 0 0.4em rgb(207, 207, 200);
}

.textArea {
  text-align: center;
  width: 80%;
  height: 60px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: none;
  padding: 2px 20px;
  margin-top: 6px;
  margin-bottom: 5px;
  font-size: 20px;
  line-height: 24px;
}
.icon_btn {
  text-align: right;
}
.dis_main {
  text-align: left;
  margin-left: 60px;
}
.btnAdd {
  width: 80%;
  height: 40px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: none;
  padding: 2px 20px;
  margin-top: 6px;
  margin-bottom: 16px;
  font-size: 17px;
  line-height: 24px;
}

.Input_Text {
  text-align: center;
}

.btnAdd:hover {
  background-color: #54cf58ce;
  color: rgb(255, 255, 255);
}

.Add_btn_main {
  background-color: #54cf58ce;
  border: none;
  color: white;
  padding: 30px 40px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 50px;
  margin: -45% 45%;
  cursor: pointer;
  border-radius: 50%;
}
.btn_div {
  margin-top: 10%;
}

.icon {
  width: 32px;
  height: 32px;
}
.icon_btn {
  margin-right: 100px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.checkIcon {
  color: #54cf58ce;
}

.time_div {
  text-transform: uppercase;
  margin-top: 3px;
  justify-content: space-between;
}

.date_h1 {
  font-size: 50px;
}

.month_h4 {
  font-size: 16px;
  margin-top: 6px;
  color: rgba(92, 89, 89, 0.973);
}
.year_h4 {
  margin-top: -8px;
  font-size: 16px;
  color: rgba(126, 122, 122, 0.973);
}

.day_h4 {
  font-size: 25px;
}

.time_div_date {
  margin-left: 60px;
  text-align: left;
}

.time_div_month {
  margin-top: 4px;
  margin-left: -190px;
}

.time_div_day {
  margin-top: 15px;
  text-align: right;
  margin-right: 60px;
}

